import { TabContext, TabList, TabPanel } from '@mui/lab'
import { Box, FormControl, FormControlLabel, Radio, RadioGroup, Stack, Tab, TextField } from '@mui/material'
import { IFileLocal } from 'interfaces'
import { useEffect, useState } from 'react'
import { renderToStaticMarkup } from 'react-dom/server'
import { AiOutlineFilePdf, AiOutlineFileUnknown, AiOutlineFileWord, AiOutlineFileZip } from 'react-icons/ai'
import { BsFiletypeTxt } from 'react-icons/bs'
import { PiFileCsvDuotone } from 'react-icons/pi'
import { RiFileExcel2Line } from 'react-icons/ri'
import { useAppSelector } from 'redux/hooks'
import DialogConfirm from './DialogConfirm'
import LocalFileDisplay from './LocalFileDisplay'

interface Props {
	target?: HTMLElement
	setValue: (val?: string) => void
}

export default function AddLinkDialog({ target: root, setValue }: Props) {
	const files = useAppSelector((s) => s.FileLocal.docs)
	const [href, setHref] = useState('')
	const [file, setFile] = useState<IFileLocal>()
	const [text, setText] = useState('')
	const [tab, setTab] = useState<'link' | 'file'>('link')
	const [target, setTarget] = useState('_self')
	useEffect(() => {
		if (root) {
			setHref(root.dataset.href || '')
			setText(root.dataset.text || '')
			const f = files.find((f) => f.path === root.dataset.file)
			if (f) {
				setTarget('_blank')
				setFile(f)
				setTab('file')
			} else {
				setTarget(root.dataset.target || '')
			}
		}
	}, [files, root])

	return (
		<>
			<DialogConfirm
				open={true}
				title="URL作成"
				maxWidth="lg"
				disabled={!text || (tab === 'link' && !href) || (tab === 'file' && !file)}
				primaryButtonColor="success"
				primaryButtonText="設定する"
				handleClose={() => {
					setValue(undefined)
				}}
				handleSubmit={() => {
					let link = ''
					switch (tab) {
						case 'file':
							if (!file) return
							const ext = file.ext
							let icon = <AiOutlineFileUnknown fontSize={20} style={{ marginTop: 2 }} color="#ccc" />
							if (ext.includes('doc')) {
								icon = <AiOutlineFileWord fontSize={20} style={{ marginTop: 2 }} color="#36f" />
							} else if (ext.includes('xls')) {
								icon = <RiFileExcel2Line fontSize={20} style={{ marginTop: 2 }} color="#0a8" />
							} else if (ext.includes('csv')) {
								icon = <PiFileCsvDuotone fontSize={20} style={{ marginTop: 2 }} color="#0a8" />
							} else if (ext.includes('pdf')) {
								icon = <AiOutlineFilePdf fontSize={20} style={{ marginTop: 2 }} color="#c00" />
							} else if (ext.includes('zip')) {
								icon = <AiOutlineFileZip fontSize={20} style={{ marginTop: 2 }} color="#e90" />
							} else if (ext.includes('txt')) {
								icon = <BsFiletypeTxt fontSize={20} style={{ marginTop: 2 }} color="#36a" />
							}
							const LINK = (
								<>
									<a href={file.path} target="_blank" rel="noreferrer">
										{text + ' '}({Math.ceil(file.size / 1024)}KB)
									</a>
									{icon}
								</>
							)
							link = renderToStaticMarkup(LINK)
							break
						default:
							link = `<a href="${href}" target="${target}">${text}</a>`
							break
					}
					setValue(`
					<div style="display:flex;align-items:center;" data-href="${href}" data-target="${target}" data-text="${text}" data-file="${
						file?.path || ''
					}">
					${link}
					</div>
					`)
				}}
			>
				<Stack direction="row" gap={2}>
					<TextField
						variant="outlined"
						required
						margin="dense"
						size="small"
						label={'表示テキスト'}
						value={text}
						InputLabelProps={{
							shrink: true,
						}}
						sx={{
							flex: 1,
						}}
						autoFocus
						onChange={(e) => setText(e.target.value)}
					/>
				</Stack>
				<Box sx={{ width: '100%', typography: 'body1' }}>
					<TabContext value={tab}>
						<Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
							<TabList
								onChange={(_, newValue) => {
									setTab(newValue)
								}}
							>
								<Tab label="LINK" value="link" />
								<Tab label="FILE" value="file" />
							</TabList>
						</Box>
						<TabPanel value="link" sx={{ px: 0, py: 2 }}>
							<TextField
								variant="outlined"
								required
								margin="dense"
								fullWidth
								size="small"
								label={'URL'}
								value={href}
								onChange={(e) => setHref(e.target.value)}
							/>
							<FormControl>
								<RadioGroup row value={target} onChange={(e) => setTarget(e.target.value)}>
									<FormControlLabel value="_self" control={<Radio />} label="同じタブ" />
									<FormControlLabel value="_blank" control={<Radio />} label="別タブ" />
								</RadioGroup>
							</FormControl>
						</TabPanel>
						<TabPanel value="file" sx={{ px: 0, py: 2 }}>
							<LocalFileDisplay
								select={file}
								handleSelect={(file) => {
									setFile(file)
									setText(file.name)
								}}
								onlyUse="doc"
							/>
						</TabPanel>
					</TabContext>
				</Box>
			</DialogConfirm>
		</>
	)
}
