import {
	Box,
	Button,
	ClickAwayListener,
	FormControl,
	FormControlLabel,
	IconButton,
	Radio,
	RadioGroup,
	Stack,
	Tooltip,
	TooltipProps,
	Typography,
	styled,
	tooltipClasses,
} from '@mui/material'
import { CSSProperties, useEffect, useState } from 'react'
import { SketchPicker } from 'react-color'
import { AiOutlineMinusCircle, AiOutlinePlusCircle } from 'react-icons/ai'
import { BsBorderStyle } from 'react-icons/bs'
import { IoColorPaletteOutline } from 'react-icons/io5'
import { MdOutlineCleaningServices } from 'react-icons/md'
import DialogConfirm from './DialogConfirm'
interface Props {
	value: CSSProperties
	setValue: (val?: CSSProperties) => void
}

const HtmlTooltip = styled(({ className, ...props }: TooltipProps) => (
	<Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
	[`& .${tooltipClasses.tooltip}`]: {
		backgroundColor: '#f5f5f9',
		color: 'rgba(0, 0, 0, 0.87)',
		maxWidth: 'none',
		fontSize: theme.typography.pxToRem(12),
		border: '1px solid #dadde9',
	},
}))

export default function AddSpacing({ value, setValue }: Props) {
	const [state, setState] = useState(value)
	useEffect(() => {
		setState(value)
	}, [value])

	return (
		<DialogConfirm
			open={true}
			title="余白設定"
			maxWidth="lg"
			primaryButtonColor="success"
			primaryButtonText="設定"
			handleClose={() => {
				setValue(undefined)
			}}
			handleSubmit={() => {
				setValue(state)
			}}
			component="div"
		>
			<Stack gap={2} justifyContent="center" alignItems="flex-start">
				<Stack flex={1} gap={1} direction="row" alignItems="center" width="100%">
					<Stack justifyContent="center" alignItems="center" flex={1} gap={1}>
						<Stack direction="row" gap={2} alignItems="center">
							<Typography>Padding</Typography>
							<Button
								variant="outlined"
								size="small"
								color="warning"
								startIcon={<MdOutlineCleaningServices />}
								onClick={() =>
									setState({ ...state, paddingTop: '0', paddingLeft: '0', paddingRight: '0', paddingBottom: '0' })
								}
							>
								リセット
							</Button>
						</Stack>
						<Stack flex={1} justifyContent="center" alignItems="center" gap={2}>
							<PlusMinus value={state.paddingTop} setValue={(val) => setState({ ...state, paddingTop: val })} />
							<Stack direction="row" gap={8}>
								<PlusMinus value={state.paddingLeft} setValue={(val) => setState({ ...state, paddingLeft: val })} />
								<PlusMinus value={state.paddingRight} setValue={(val) => setState({ ...state, paddingRight: val })} />
							</Stack>
							<PlusMinus value={state.paddingBottom} setValue={(val) => setState({ ...state, paddingBottom: val })} />
						</Stack>
					</Stack>
					<Stack justifyContent="center" alignItems="center" flex={1} gap={1}>
						<Stack direction="row" gap={2} alignItems="center">
							<Typography>Margin</Typography>
							<Button
								variant="outlined"
								size="small"
								color="warning"
								startIcon={<MdOutlineCleaningServices />}
								onClick={() =>
									setState({ ...state, marginTop: '0', marginLeft: '0', marginRight: '0', marginBottom: '0' })
								}
							>
								リセット
							</Button>
						</Stack>
						<Stack flex={1} justifyContent="center" alignItems="center" gap={4}>
							<PlusMinus value={state.marginTop} setValue={(val) => setState({ ...state, marginTop: val })} />
							<Stack direction="row" gap={8}>
								<PlusMinus value={state.marginLeft} setValue={(val) => setState({ ...state, marginLeft: val })} />
								<PlusMinus value={state.marginRight} setValue={(val) => setState({ ...state, marginRight: val })} />
							</Stack>
							<PlusMinus value={state.marginBottom} setValue={(val) => setState({ ...state, marginBottom: val })} />
						</Stack>
					</Stack>
					<Stack justifyContent="center" alignItems="center" flex={1} gap={1}>
						<Stack direction="row" gap={2} alignItems="center">
							<Typography fontWeight={600}>Border</Typography>
							<Button
								variant="outlined"
								size="small"
								color="warning"
								startIcon={<MdOutlineCleaningServices />}
								onClick={() =>
									setState({
										...state,
										borderTopWidth: '0',
										borderLeftWidth: '0',
										borderRightWidth: '0',
										borderBottomWidth: '0',
										borderTopStyle: 'solid',
										borderLeftStyle: 'solid',
										borderRightStyle: 'solid',
										borderBottomStyle: 'solid',
										borderTopColor: 'currentcolor',
										borderLeftColor: 'currentcolor',
										borderRightColor: 'currentcolor',
										borderBottomColor: 'currentcolor',
									})
								}
							>
								リセット
							</Button>
						</Stack>
						<Stack flex={1} width="100%" maxWidth={400} justifyContent="center" alignItems="center" gap={2}>
							<Stack direction="row">
								<PlusMinus
									unit={1}
									value={state.borderTopWidth}
									setValue={(val) => setState({ ...state, borderTopWidth: val })}
								/>
								<Border value={state.borderTopStyle} setValue={(val) => setState({ ...state, borderTopStyle: val })} />
								<ColorPanel
									value={state.borderTopStyle}
									setValue={(val) => setState({ ...state, borderTopStyle: val })}
								/>
							</Stack>
							<Stack direction="row" justifyContent="space-between" width="100%">
								<Stack direction="row">
									<PlusMinus
										unit={1}
										value={state.borderLeftWidth}
										setValue={(val) => setState({ ...state, borderLeftWidth: val })}
									/>
									<Border
										value={state.borderLeftStyle}
										setValue={(val) => setState({ ...state, borderLeftStyle: val })}
									/>
									<ColorPanel
										value={state.borderLeftColor}
										setValue={(val) => setState({ ...state, borderLeftColor: val })}
									/>
								</Stack>
								<Stack direction="row">
									<PlusMinus
										unit={1}
										value={state.borderRightWidth}
										setValue={(val) => setState({ ...state, borderRightWidth: val })}
									/>
									<Border
										value={state.borderRightStyle}
										setValue={(val) => setState({ ...state, borderRightStyle: val })}
									/>
									<ColorPanel
										value={state.borderRightColor}
										setValue={(val) => setState({ ...state, borderRightColor: val })}
									/>
								</Stack>
							</Stack>
							<Stack direction="row">
								<PlusMinus
									unit={1}
									value={state.borderBottomWidth}
									setValue={(val) => setState({ ...state, borderBottomWidth: val })}
								/>
								<Border
									value={state.borderBottomStyle}
									setValue={(val) => setState({ ...state, borderBottomStyle: val })}
								/>
								<ColorPanel
									value={state.borderBottomColor}
									setValue={(val) => setState({ ...state, borderBottomColor: val })}
								/>
							</Stack>
						</Stack>
					</Stack>
				</Stack>
				<Stack height={250} width="100%" justifyContent="center">
					<Stack sx={{ border: '1px dotted #a1c', width: '100%' }}>
						<Box sx={{ width: '100%', height: 50, bgcolor: '#976' }}></Box>
						<Stack
							alignItems="center"
							justifyContent="center"
							sx={{
								bgcolor: '#1902',
								...state,
							}}
						>
							<p style={{ background: '#eee5' }}>コンテンツ</p>
						</Stack>
						<Box sx={{ width: '100%', height: 50, bgcolor: '#976' }}></Box>
					</Stack>
				</Stack>
			</Stack>
		</DialogConfirm>
	)
}

function PlusMinus({
	value = '0px',
	setValue,
	unit = 4,
}: {
	value?: string | number
	setValue: (val: string) => void
	unit?: number
}) {
	console.log(value)
	return (
		<Stack direction="row" alignItems="center">
			<Button
				variant="text"
				sx={{
					minWidth: 0,
					p: 0.2,
					width: 36,
					height: 36,
					fontSize: 24,
				}}
				onClick={() => setValue((parseInt(`${value}`) || 0) - unit + 'px')}
				disabled={!value || parseInt(`${value}`) <= 0}
			>
				<AiOutlineMinusCircle />
			</Button>
			<Typography sx={{ userSelect: 'none', minWidth: 30, textAlign: 'center' }}>{value || '0px'}</Typography>
			<Button
				variant="text"
				sx={{
					minWidth: 0,
					p: 0.2,
					width: 36,
					height: 36,
					fontSize: 24,
				}}
				onClick={() => setValue((parseInt(`${value}`) || 0) + unit + 'px')}
			>
				<AiOutlinePlusCircle />
			</Button>
		</Stack>
	)
}

function Border({ value, setValue }: { value: any; setValue: (val: any) => void }) {
	const [open, setOpen] = useState(false)
	return (
		<>
			<ClickAwayListener onClickAway={() => setOpen(false)}>
				<div>
					<HtmlTooltip
						PopperProps={{
							disablePortal: true,
						}}
						arrow
						onClose={() => setOpen(false)}
						open={open}
						disableFocusListener
						disableHoverListener
						disableTouchListener
						title={
							<FormControl>
								<RadioGroup row value={value} onChange={(e) => setValue(e.target.value)}>
									<FormControlLabel value="solid" control={<Radio />} label="solid" />
									<FormControlLabel value="dotted" control={<Radio />} label="dotted" />
									<FormControlLabel value="dashed" control={<Radio />} label="dashed" />
									<FormControlLabel value="double" control={<Radio />} label="double" />
									<FormControlLabel value="groove" control={<Radio />} label="groove" />
								</RadioGroup>
							</FormControl>
						}
					>
						<IconButton color="info" onClick={() => setOpen(true)}>
							<BsBorderStyle />
						</IconButton>
					</HtmlTooltip>
				</div>
			</ClickAwayListener>
		</>
	)
}
function ColorPanel({ value, setValue }: { value: any; setValue: (val: any) => void }) {
	const [open, setOpen] = useState(false)
	return (
		<>
			<ClickAwayListener onClickAway={() => setOpen(false)}>
				<div>
					<HtmlTooltip
						PopperProps={{
							disablePortal: true,
						}}
						arrow
						onClose={() => setOpen(false)}
						open={open}
						disableFocusListener
						disableHoverListener
						disableTouchListener
						title={
							<SketchPicker
								color={value}
								onChange={(color) => {
									setValue(color.hex)
								}}
							/>
						}
					>
						<IconButton color="success" onClick={() => setOpen(true)}>
							<IoColorPaletteOutline />
						</IconButton>
					</HtmlTooltip>
				</div>
			</ClickAwayListener>
		</>
	)
}
