import SearchIcon from '@mui/icons-material/Search'
import { Box, InputAdornment, Stack, TextField, Typography } from '@mui/material'
import { format } from 'date-fns'
import { IForm, IFormItem } from 'interfaces'
import { useCallback, useEffect, useRef, useState } from 'react'
import { renderToStaticMarkup } from 'react-dom/server'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getForms } from 'redux/slices/Form'
import DialogConfirm from './DialogConfirm'

interface Props {
	setValue: (val?: { formId: number; elm: HTMLFormElement }) => void
}

export default function AddFormDialog({ setValue }: Props) {
	const dispatch = useAppDispatch()
	const formListRef = useRef<HTMLDivElement>(null)
	const forms = useAppSelector((s) => s.Form.list)
	const status = useAppSelector((s) => s.Form.status)
	useEffect(() => {
		if (status === 'init') dispatch(getForms())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	const [search, setSearch] = useState('')
	const [formSelected, setFormSelected] = useState<IForm>()

	const getInput = useCallback((item: IFormItem, branch?: boolean) => {
		const {
			format,
			display,
			maxlength,
			minlength,
			multiSelect,
			options,
			required,
			textType,
			placeholder,
			rows,
			useTime,
			name,
		} = item
		let input: JSX.Element = <></>

		const LabelItem = ({ useFor = true }: { useFor?: boolean }) =>
			useFor ? (
				<>
					<label
						htmlFor={name}
						style={{
							display: 'flex',
							alignItems: 'center',
							margin: '10px 0 4px',
						}}
					>
						{display}
						{required ? (
							<span
								style={{
									fontWeight: 400,
									color: '#EC0000',
									marginLeft: 4,
									fontSize: 14,
								}}
							>
								必須
							</span>
						) : null}
					</label>
				</>
			) : (
				<label
					style={{
						display: 'flex',
						alignItems: 'center',
						margin: '10px 0 4px',
					}}
				>
					{display}
					{required ? (
						<span
							style={{
								fontWeight: 400,
								color: '#EC0000',
								marginLeft: 4,
								fontSize: 14,
							}}
						>
							必須
						</span>
					) : null}
				</label>
			)
		switch (format) {
			case '文章（回答なし）':
				input = <LabelItem useFor={false} />
				break
			case 'メールアドレス':
				input = (
					<>
						<LabelItem />
						<input
							type="email"
							placeholder={placeholder}
							style={{
								width: '100%',
								lineHeight: '32px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
								fontSize: '0.9rem',
							}}
							name={name}
							id={name}
							minLength={parseInt(`${minlength}`) || undefined}
							maxLength={parseInt(`${maxlength}`) || undefined}
						/>
					</>
				)
				break
			case '短文':
				let pattern: string | undefined = undefined
				if (textType === 'en') pattern = `[A-z\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'num') pattern = `[0-9\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'en-num') pattern = `[A-z0-9\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'en-num-sym') pattern = `[A-Za-z0-9\\p{P}\\p{S}\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'ja')
					pattern = `[一-龯ぁ-ゔゞ゛゜ーァ-・ヽヾ゛゜ー０-９　]{${minlength || 0},${maxlength || ''}}`

				input = (
					<>
						<LabelItem />
						<input
							type="text"
							placeholder={placeholder}
							style={{
								width: '100%',
								lineHeight: '32px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
								fontSize: '0.9rem',
							}}
							name={name}
							id={name}
							minLength={parseInt(`${minlength}`) || undefined}
							maxLength={parseInt(`${maxlength}`) || undefined}
							pattern={pattern}
						/>
					</>
				)
				break
			case '長文':
				input = (
					<>
						<LabelItem />
						<textarea
							placeholder={placeholder}
							style={{
								width: '100%',
								lineHeight: '24px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
								resize: rows ? 'none' : 'vertical',
								fontSize: '1rem',
							}}
							name={name}
							id={name}
							rows={parseInt(`${rows}`) || 4}
							minLength={parseInt(`${minlength}`) || undefined}
							maxLength={parseInt(`${maxlength}`) || undefined}
						/>
					</>
				)

				break
			case '日付':
				input = (
					<>
						<LabelItem useFor={false} />
						<input
							type={useTime ? 'datetime-local' : 'date'}
							style={{
								width: '100%',
								lineHeight: '32px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
							}}
							name={name}
							id={name}
							min={minlength}
							max={maxlength}
						/>
					</>
				)
				break
			case '選択肢':
				input = (
					<>
						<LabelItem useFor={false} />
						<div
							style={{
								display: 'flex',
								flexDirection: 'column',
							}}
						>
							{options.map((v) => {
								return (
									<div
										key={v.id}
										style={{
											display: 'flex',
											padding: '2px 10px',
											gap: '4px',
											margin: '2px 0',
										}}
									>
										<input
											type={multiSelect ? 'checkbox' : 'radio'}
											name={name}
											id={v.id}
											value={branch ? v.text : v.id}
										/>
										<label htmlFor={v.id} style={{ fontWeight: 400 }}>
											{v.text}
										</label>
									</div>
								)
							})}
						</div>
					</>
				)

				break
			case '一覧':
				input = (
					<>
						<LabelItem useFor={false} />
						<select
							name={name}
							id={name}
							style={{
								width: '100%',
								height: 42,
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
							}}
						>
							<option value={''}>--</option>
							{options.map((v) => {
								return (
									<option value={v.text} key={v.id}>
										{v.text}
									</option>
								)
							})}
						</select>
					</>
				)

				break

			default:
				break
		}
		return (
			<div
				style={{
					display: branch ? 'none' : 'block',
					marginBottom: '1rem',
				}}
				data-name={name}
				data-required={required ? 'true' : undefined}
				data-maxlength={maxlength}
			>
				{input}
			</div>
		)
	}, [])
	return (
		<>
			<DialogConfirm
				open={true}
				title="フォーム追加"
				maxWidth="md"
				primaryButtonColor="success"
				primaryButtonText="設定する"
				disabled={!formSelected}
				handleClose={() => {
					setValue(undefined)
				}}
				handleSubmit={() => {
					if (!formSelected) return
					const items: string[] = []
					const styles: string[] = []
					formSelected.items.forEach((v) => {
						items.push(renderToStaticMarkup(<>{getInput(v)}</>))
						if (v.branch && v.branchItems?.length) {
							v.branchItems.forEach((child) => {
								child.list.forEach((item) => {
									styles.push(`
									[data-name="${v.name}"]:has(>div input#${child.option}:checked) ~ [data-name="${item.name}"] {
										display:block !important;
									}
									`)
									items.push(renderToStaticMarkup(<>{getInput(item, true)}</>))
								})
							})
						}
					})

					const actions = renderToStaticMarkup(
						<div
							style={{
								position: 'relative',
								display: 'flex',
								flexWrap: 'wrap',
								justifyContent: 'space-around',
								alignItems: 'center',
								margin: '2rem auto',
								maxWidth: 1024,
							}}
						>
							<button
								type="reset"
								style={{
									display: 'flex',
									width: 256,
									height: 56,
									justifyContent: 'center',
									alignItems: 'center',
									background: '#475569',
									borderRadius: 4,
									border: 'none',
									fontSize: 15,
									color: '#fff',
									fontWeight: 500,
									cursor: 'pointer',
									transition: 'all 200ms',
									margin: '10px 0',
								}}
							>
								リセット
							</button>
							<button
								type="submit"
								style={{
									display: 'flex',
									width: 256,
									height: 56,
									justifyContent: 'center',
									alignItems: 'center',
									background: '#1D4ED8',
									borderRadius: 4,
									border: 'none',
									fontSize: 15,
									color: '#fff',
									fontWeight: 500,
									cursor: 'pointer',
									transition: 'all 200ms',
									margin: '10px 0',
								}}
							>
								確認画面へ
							</button>
						</div>
					)
					const form = document.createElement('form')
					form.action = `/form/${formSelected.id}/confirm`
					form.method = 'POST'
					form.id = 'form-' + formSelected.id
					const sd = new Date(formSelected.startDate || 0)
					const ed = new Date(formSelected.endDate || 4102412400000)
					form.innerHTML = `
					${
						formSelected.startDate || formSelected.endDate
							? `<noscript>
					<p style='text-align:center;color:#EC0000;'>
					本フォームの受付期間は、${formSelected.startDate ? format(sd, 'yyyy年MM月dd日') + 'から' : ''}${
									formSelected.endDate ? format(ed, 'yyyy年MM月dd日') + 'まで' : ''
							  }です。
					</p>
					<p style='text-align:center;color:#EC0000;'>期間外の回答は無効となります、ご了承ください。</p>
					</noscript>`
							: ''
					}
					${items.join('\n')}
					${actions}
					<script>
						function hiddenForm(){
							const startDate = new Date(${sd.getTime()})
							const endDate = new Date(${ed.getTime()})
							const form = document.getElementById("form-" + ${formSelected.id})
							if(form && (startDate > new Date() || endDate < new Date())){
								form.innerHTML = "<p style='text-align:center;color:#EC0000;'>本フォームの受付期間は、${
									formSelected.startDate ? format(sd, 'yyyy年MM月dd日') + 'から' : ''
								}${
						formSelected.endDate ? format(ed, 'yyyy年MM月dd日') + 'まで' : ''
					}です。</p><p style='text-align:center;color:#EC0000;'>期間外の回答は無効となります、ご了承ください。</p>"
							}
						}
						hiddenForm()
						window.addEventListener("load", hiddenForm)
						function showLengthCanInput(){
							document.querySelectorAll("[data-maxlength]").forEach(
								wrap=>{
									var input = wrap.querySelector('input[type="text"],textarea')
									var p = wrap.querySelector('p.length') 
									if(!p){
										p = document.createElement("p")
										p.className = "length"
										p.style.fontSize = '0.9rem'
										wrap.appendChild(p)
									}
									p.innerText = "残り文字数: "+ (wrap.dataset.maxlength - input.value.length)
									input.oninput=()=>{
										const text = input.value
											p.innerText = "残り文字数: "+ (wrap.dataset.maxlength - text.length)
										}
								}
							)
						}
						showLengthCanInput()
						window.addEventListener("load",showLengthCanInput)
					</script>
					`
					const style = document.createElement('style')
					style.appendChild(document.createTextNode(styles.join('\n')))
					form.appendChild(style)

					setValue({ formId: formSelected.id, elm: form })
				}}
			>
				<Stack ref={formListRef}>
					<Stack
						direction="row"
						alignItems="center"
						gap={1}
						sx={{
							bgcolor: '#eee',
							border: '1px solid #ccc',
							px: 1,
							py: 0.5,
						}}
					>
						<Typography flex={1} variant="h6">
							フォーム一覧
						</Typography>
						<Box flex={1}>
							<TextField
								variant="outlined"
								margin="dense"
								fullWidth
								size="small"
								InputProps={{
									startAdornment: (
										<InputAdornment position="start">
											<SearchIcon />
										</InputAdornment>
									),
								}}
								label={'フィルター'}
								InputLabelProps={{ shrink: true }}
								value={search}
								onChange={(e) => setSearch(e.target.value)}
							/>
						</Box>
					</Stack>
					<Stack
						sx={{
							height: '50vh',
							overflow: 'auto',
							border: '1px solid #ccc',
						}}
					>
						<Stack
							direction="row"
							alignItems="center"
							gap={1}
							sx={{
								p: 1,
								bgcolor: '#ccc',
							}}
						>
							<Typography flex={1}>フォーム名</Typography>
							<Typography textAlign="center" width={150} borderLeft="1px solid #ccc">
								開始日
							</Typography>
							<Typography textAlign="center" width={150} borderLeft="1px solid #ccc">
								終了日
							</Typography>
						</Stack>
						{forms
							.filter((f) => f.name.includes(search))
							.map((v) => {
								return (
									<Stack
										direction="row"
										alignItems="center"
										key={v.id}
										gap={1}
										sx={{
											px: 1,
											py: 0.2,
											bgcolor: v.id === formSelected?.id ? '#75f3 !important' : 'transparent',
											'&:hover': {
												bgcolor: '#eee',
												cursor: 'pointer',
											},
										}}
										onClick={() => setFormSelected(v)}
									>
										<Typography flex={1}>{v.name}</Typography>
										<Typography textAlign="center" width={150} borderLeft="1px solid #ccc">
											{v.startDate ? format(new Date(v.startDate), 'yyyy年MM月dd日') : '--'}
										</Typography>
										<Typography textAlign="center" width={150} borderLeft="1px solid #ccc">
											{v.endDate ? format(new Date(v.endDate), 'yyyy年MM月dd日') : '--'}
										</Typography>
									</Stack>
								)
							})}
					</Stack>
				</Stack>
			</DialogConfirm>
		</>
	)
}
