import { Box, FormControl, FormControlLabel, Radio, RadioGroup, Stack, TextField, Typography } from '@mui/material'
import { IFileLocal } from 'interfaces'
import { useEffect, useState } from 'react'
import { renderToStaticMarkup } from 'react-dom/server'
import { useAppSelector } from 'redux/hooks'
import BottomButton from './Button/Bottom'
import CenterButton from './Button/Center'
import LeftButton from './Button/Left'
import MiddleButton from './Button/Middle'
import RightButton from './Button/Right'
import TopButton from './Button/Top'
import DialogConfirm from './DialogConfirm'
import LocalFileDisplay from './LocalFileDisplay'

interface Props {
	img?: HTMLElement | null
	setValue: (val?: string) => void
}

export default function AddImageDialog({ setValue, img }: Props) {
	const images = useAppSelector((s) => s.FileLocal.images)
	const [file, setFile] = useState<IFileLocal>()
	const [href, setHref] = useState<string>()
	const [alt, setAlt] = useState<string>('')
	const [target, setTarget] = useState<string>('_blank')
	const [align, setAlign] = useState({
		horizontal: 'center',
		vertical: 'center',
	})
	const [size, setSize] = useState<{
		width: number
		height: number
	}>({
		width: 0,
		height: 0,
	})

	useEffect(() => {
		if (img) {
			const { path, width, height, vertical, horizontal, target, href, alt } = img.dataset
			setHref(href)
			setTarget(target || '_blank')
			setAlign({
				horizontal: horizontal || 'center',
				vertical: vertical || 'center',
			})
			setAlt(alt || '')
			setSize({ width: width ? parseInt(width) : 0, height: height ? parseInt(height) : 0 })
			const f = images.find((f) => f.path === path)
			setFile(f)
		}
	}, [images, img])

	return (
		<>
			<DialogConfirm
				open={true}
				title=""
				maxWidth="lg"
				disabled={!file}
				primaryButtonColor="success"
				primaryButtonText="設定"
				handleClose={() => {
					setValue(undefined)
				}}
				handleSubmit={() => {
					if (!file) return
					const imgTag = renderToStaticMarkup(
						<div
							style={{
								width: '100%',
								height: 'auto',
								display: 'flex',
								justifyContent:
									align.vertical === 'left' ? 'flex-start' : align.vertical === 'right' ? 'flex-end' : align.vertical,
								alignItems:
									align.horizontal === 'top'
										? 'flex-start'
										: align.horizontal === 'bottom'
										? 'flex-end'
										: align.horizontal,
							}}
							data-width={size.width || ''}
							data-height={size.height || ''}
							data-path={file.path}
							data-vertical={align.vertical}
							data-horizontal={align.horizontal}
							data-href={href}
							data-target={target}
							data-alt={alt}
						>
							{href ? (
								<a href={href} target={target}>
									<img
										src={file.path}
										alt={alt}
										style={{
											width: size.width || '100%',
											height: size.height || 'auto',
											overflow: 'hidden',
											objectFit: 'contain',
											objectPosition: `${align.vertical} ${align.horizontal}`,
										}}
									/>
								</a>
							) : (
								<img
									src={file.path}
									alt={alt}
									style={{
										width: size.width || '100%',
										height: size.height || 'auto',
										overflow: 'hidden',
										objectFit: 'contain',
										objectPosition: `${align.vertical} ${align.horizontal}`,
									}}
								/>
							)}
						</div>
					)
					setValue(imgTag)
				}}
			>
				<Stack gap={2}>
					<Box sx={{ width: '100%' }}>
						<LocalFileDisplay
							handleSelect={(file) => {
								setFile(file)
							}}
							select={file}
							onlyUse="img"
							imgSize={150}
							maxHeight={300}
						/>
					</Box>
					<Stack direction="row" alignItems="center" gap={2} mt={2}>
						<Typography fontWeight={600} minWidth={140}>
							説明
						</Typography>
						<TextField
							variant="outlined"
							size="small"
							fullWidth
							multiline
							minRows={2}
							placeholder="代替テキストを入力してください"
							InputLabelProps={{ shrink: true }}
							value={alt}
							onChange={(e) => {
								setAlt(e.target.value)
							}}
						/>
					</Stack>
					<Stack direction="row" alignItems="center" gap={2}>
						<Typography fontWeight={600} minWidth={140}>
							配置
						</Typography>
						<Stack gap={2} sx={{ color: '#444' }}>
							<Stack direction="row" justifyContent="center" alignItems="center" gap={1}>
								<TopButton
									color={align.horizontal === 'top' ? 'info' : 'inherit'}
									variant={align.horizontal === 'top' ? 'contained' : 'outlined'}
									onClick={() => {
										setAlign({
											...align,
											horizontal: 'top',
										})
									}}
								/>
								<MiddleButton
									color={align.horizontal === 'center' ? 'info' : 'inherit'}
									variant={align.horizontal === 'center' ? 'contained' : 'outlined'}
									onClick={() => {
										setAlign({
											...align,
											horizontal: 'center',
										})
									}}
								/>
								<BottomButton
									color={align.horizontal === 'bottom' ? 'info' : 'inherit'}
									variant={align.horizontal === 'bottom' ? 'contained' : 'outlined'}
									onClick={() => {
										setAlign({
											...align,
											horizontal: 'bottom',
										})
									}}
								/>
							</Stack>
							<Stack direction="row" justifyContent="center" alignItems="center" gap={1}>
								<LeftButton
									color={align.vertical === 'left' ? 'info' : 'inherit'}
									variant={align.vertical === 'left' ? 'contained' : 'outlined'}
									onClick={() => {
										setAlign({
											...align,
											vertical: 'left',
										})
									}}
								/>
								<CenterButton
									color={align.vertical === 'center' ? 'info' : 'inherit'}
									variant={align.vertical === 'center' ? 'contained' : 'outlined'}
									onClick={() => {
										setAlign({
											...align,
											vertical: 'center',
										})
									}}
								/>
								<RightButton
									color={align.vertical === 'right' ? 'info' : 'inherit'}
									variant={align.vertical === 'right' ? 'contained' : 'outlined'}
									onClick={() => {
										setAlign({
											...align,
											vertical: 'right',
										})
									}}
								/>
							</Stack>
						</Stack>
					</Stack>
					<Stack direction="row" alignItems="center" gap={2} mt={2}>
						<Typography fontWeight={600} minWidth={140}>
							サイズ
						</Typography>
						<TextField
							variant="outlined"
							size="small"
							fullWidth
							label={'横幅'}
							placeholder="自動"
							InputLabelProps={{ shrink: true }}
							value={size?.width || ''}
							onChange={(e) => {
								const val = parseInt(e.target.value)
								setSize({
									...size,
									width: val,
								})
							}}
							sx={{
								width: 100,
							}}
						/>
						<p>X</p>
						<TextField
							variant="outlined"
							size="small"
							fullWidth
							label={'縦幅'}
							placeholder="自動"
							InputLabelProps={{ shrink: true }}
							value={size?.height || ''}
							onChange={(e) => {
								const val = parseInt(e.target.value)
								setSize({
									...size,
									height: val,
								})
							}}
							sx={{
								width: 100,
							}}
						/>
					</Stack>
					<Stack direction="row" alignItems="center" gap={2} mt={2}>
						<Typography fontWeight={600} minWidth={140}>
							リンク付け
						</Typography>
						<TextField
							variant="outlined"
							margin="dense"
							size="small"
							label={'URL'}
							value={href}
							onChange={(e) => setHref(e.target.value)}
						/>
						<FormControl>
							<RadioGroup row value={target} onChange={(e) => setTarget(e.target.value)}>
								<FormControlLabel value="_self" control={<Radio />} label="同じタブ" />
								<FormControlLabel value="_blank" control={<Radio />} label="別タブ" />
							</RadioGroup>
						</FormControl>
					</Stack>
				</Stack>
			</DialogConfirm>
		</>
	)
}
