import { Autocomplete, TextField } from '@mui/material'
import { PERMISSION } from 'interfaces'
import { useEffect, useMemo } from 'react'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getGroups } from 'redux/slices/Group'

interface IGroup {
	id: number
	name: string
}
interface Props {
	value: number[]
	setValue: (val: number[]) => void
}
export default function SubOwnerSelect({ value, setValue }: Props) {
	const dispatch = useAppDispatch()
	const status = useAppSelector((s) => s.Group.status)
	const list = useAppSelector((s) => s.Group.list)
	useEffect(() => {
		if (status === 'init') dispatch(getGroups())
	}, [status, dispatch])
	const readOnlyGroup: IGroup[] = list
		.filter((f) => f.permission[PERMISSION.CMS] === 1)
		.map((v) => ({ id: v.id, name: v.name }))

	const selected = useMemo(() => {
		return readOnlyGroup.filter((f) => value.includes(f.id))
	}, [readOnlyGroup, value])

	return (
		<Autocomplete
			multiple
			options={readOnlyGroup}
			getOptionLabel={(option) => option.name}
			getOptionDisabled={(option) => selected.findIndex((f) => f.id === option.id) >= 0}
			fullWidth
			sx={{
				maxWidth: 500,
			}}
			size="small"
			renderInput={(params) => <TextField {...params} size="small" variant="outlined" label="副編集者を選択" />}
			disableCloseOnSelect
			renderOption={(props, option) => {
				if (selected.findIndex((f) => f.id === option.id) >= 0) return null
				return <li {...props}>{option.name}</li>
			}}
			value={selected}
			onChange={(_event: any, newValue) => {
				setValue(newValue.map((v) => v.id))
			}}
		/>
	)
}
