import { TabContext, TabList, TabPanel } from '@mui/lab'
import { Box, Button, FormControl, FormControlLabel, Radio, RadioGroup, Stack, Tab, Typography } from '@mui/material'
import { useEffect, useState } from 'react'
import { CompactPicker } from 'react-color'
import { useAppSelector } from 'redux/hooks'
import DialogConfirm from './DialogConfirm'
import LocalFileDisplay from './LocalFileDisplay'
interface Props {
	value: {
		bgImage?: string
		bgColor?: string
		color?: string
		bgPosition?: string
		bgRepeat?: string
		bgSize?: string
	}
	setValue: (val?: {
		bgImage?: string
		bgColor?: string
		color: string
		bgPosition?: string
		bgRepeat?: string
		bgSize?: string
	}) => void
}
export default function AddBackground({ value, setValue }: Props) {
	const [bgColor, setBgColor] = useState('')
	const [bgPosition, setBgPosition] = useState<'top' | 'bottom' | 'left' | 'right' | 'center'>('center')
	const [bgRepeat, setBgRepeat] = useState<'repeat-x' | 'repeat-y' | 'repeat' | 'no-repeat'>('no-repeat')
	const [bgSize, setBgSize] = useState<'cover' | 'contain' | 'unset'>('cover')
	const [bgImage, setBgImage] = useState<string>()
	const [color, setColor] = useState('')
	const images = useAppSelector((s) => s.FileLocal.images)
	const [tab, setTab] = useState<'color' | 'img'>('color')
	useEffect(() => {
		value.bgImage && setTab('img')
		setBgColor(value.bgColor || '')
		setBgPosition((value.bgPosition as any) || 'center')
		setBgRepeat((value.bgRepeat as any) || 'no-repeat')
		setBgSize((value.bgSize as any) || 'cover')
		setBgImage(value.bgImage)
		setColor(value.color || '')
	}, [value])

	return (
		<DialogConfirm
			open={true}
			title=""
			maxWidth="lg"
			primaryButtonColor="success"
			primaryButtonText="設定"
			handleClose={() => {
				setValue(undefined)
			}}
			handleSubmit={() => {
				setValue(
					tab === 'img'
						? {
								bgImage,
								bgColor: '',
								color,
								bgPosition,
								bgRepeat,
								bgSize,
						  }
						: { bgColor, color }
				)
			}}
			component="div"
		>
			<Stack gap={2}>
				<TabContext value={tab}>
					<Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
						<TabList
							onChange={(_, newValue) => {
								setTab(newValue)
							}}
						>
							<Tab label="背景色" value="color" />
							<Tab label="背景画像" value="img" />
						</TabList>
					</Box>
					<TabPanel value="color" sx={{ px: 0, py: 2 }}>
						<Stack direction="row" gap={2} alignItems="center">
							<Typography variant="h6" minWidth={200}>
								背景色
							</Typography>
							<CompactPicker
								color={bgColor}
								onChange={(color) => {
									setBgColor(color.hex)
								}}
							/>
							<Button color="info" variant="outlined" onClick={() => setBgColor('transparent')}>
								透明
							</Button>
						</Stack>
					</TabPanel>
					<TabPanel value="img" sx={{ px: 0, py: 2 }}>
						<LocalFileDisplay
							handleSelect={(file) => {
								setBgImage(file.path)
							}}
							select={images.find((f) => f.path === bgImage)}
							onlyUse="img"
							imgSize={150}
							maxHeight={300}
						/>
						<Stack gap={2}>
							<FormControl>
								<RadioGroup row value={bgRepeat} onChange={(e) => setBgRepeat(e.target.value as any)}>
									<FormControlLabel value="repeat-x" control={<Radio />} label="repeat 横" />
									<FormControlLabel value="repeat-y" control={<Radio />} label="repeat 縦" />
									<FormControlLabel value="repeat" control={<Radio />} label="repeat" />
									<FormControlLabel value="no-repeat" control={<Radio />} label="no-repeat" />
								</RadioGroup>
							</FormControl>
							<FormControl>
								<RadioGroup row value={bgSize} onChange={(e) => setBgSize(e.target.value as any)}>
									<FormControlLabel value="cover" control={<Radio />} label="cover" />
									<FormControlLabel value="contain" control={<Radio />} label="contain" />
								</RadioGroup>
							</FormControl>
							<FormControl>
								<RadioGroup row value={bgPosition} onChange={(e) => setBgPosition(e.target.value as any)}>
									<FormControlLabel value="top" control={<Radio />} label="top" />
									<FormControlLabel value="bottom" control={<Radio />} label="bottom" />
									<FormControlLabel value="left" control={<Radio />} label="left" />
									<FormControlLabel value="right" control={<Radio />} label="right" />
									<FormControlLabel value="center" control={<Radio />} label="center" />
								</RadioGroup>
							</FormControl>
						</Stack>
					</TabPanel>
				</TabContext>
				<Stack direction="row" gap={2} alignItems="center">
					<Typography variant="h6" minWidth={200}>
						テキスト色
					</Typography>
					<CompactPicker
						color={color}
						onChange={(color) => {
							setColor(color.hex)
						}}
					/>
					<Button color="info" variant="outlined" onClick={() => setColor('currentColor')}>
						元の色
					</Button>
				</Stack>
			</Stack>
		</DialogConfirm>
	)
}
